import { useContext } from "react";
import locales from "../locales";
import { DatepickerContext } from "../provider";
var useTranslate = function () {
    var config = useContext(DatepickerContext);
    var _t = function (text, params) {
        var dic = locales[config.lang];
        if (dic)
            text = dic[text] ? dic[text] : text;
        if (params) {
            params.forEach(function (param, index) {
                text.replace("{".concat(index, "}"), param);
            });
        }
        return text;
    };
    return { _t: _t };
};
export default useTranslate;
