import { useContext } from "react";
import { DatepickerContext } from "../provider";
var usePersian = function () {
    var config = useContext(DatepickerContext);
    var convertNumbers = function (number) {
        if (config.lang === "fa") {
            var farsiDigits_1 = ["", "", "", "", "", "", "", "", "", ""];
            return number
                .toString()
                .split("")
                .map(function (x) { return farsiDigits_1[parseInt(x)]; })
                .join("");
        }
        return number;
    };
    return { convertNumbers: convertNumbers };
};
export default usePersian;
